import Vue from "vue"
import booklimit from "./components/booklimit"
import tpldescription from "@/components/tpldescription"
import './index.css' // 载入样式

let vue = new Vue({
  el: '#app',
  components: {
    booklimit,
    tpldescription
  },
  mounted() {
    this.loadDatas()
  },
  methods: {
    loadDatas() {
      let that = this;
      let loading = that.$loading({
        lock: true
      });
      Promise.all([
        that.$refs.booklimit.loadDatas(),
        that.$refs.booknotice.loadDatas(),
        that.$refs.returnrule.loadDatas()
      ]).then(res => {
        loading && loading.close();
      }).catch(err => {
        loading && loading.close();
        this.$message({
          type: 'error',
          message: err
        })
      });
    },
    saveDatas: function () {
      var that = this;
      let loading = that.$loading({
        lock: true
      });
      Promise.all([
        that.$refs.booknotice.checkTplData(),
        that.$refs.returnrule.checkTplData()
      ]).then(res => {
        return Promise.all([
          that.$refs.booklimit.saveData(),
          that.$refs.booknotice.saveTplData(),
          that.$refs.returnrule.saveTplData()
        ]);
      }).then(resList => {
        loading && loading.close();
        //处理结果呈现
        var result = {
          success: true,
          msg: ''
        };
        resList.forEach((v, i) => {
          result.success = result.success && v.success;
          v.msg && (result.msg += `<p>${v.msg}</p>`);
        });
        that.$confirm(result.msg, "保存结果", {
          confirmButtonText: "确定",
          type: result.success ? "success" : "warning",
          center: true,
          showCancelButton: false,
          dangerouslyUseHTMLString: true
        }).then(_ => {

        });
      }).catch(err => {
        that.$message({
          type: 'error',
          message: err
        });
        loading && loading.close();
      })
    }
  }
});


$(function () {
  $("#saveProductInfo").click(function () {
    vue.saveDatas();
  });
});



