<template>
    <glui-table :data='modules' :show-header='false'>
        <glui-table-column prop="Name" width="100">
        </glui-table-column>
        <glui-table-column>
            <template slot-scope="scope">
                <ul v-if="scope.row.Module==6">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div class="chkText">
                                该产品为特定主题团，单订单出游人数若全为
                                <glui-select v-model="item.MinValue" placeholder="请选择">
                                    <glui-option :value="0" label="请选择">
                                    </glui-option>
                                    <glui-option :value="1" label="男性">
                                    </glui-option>
                                    <glui-option :value="2" label="女性">
                                    </glui-option>
                                </glui-select>
                                ，需现询确定，敬请谅解！
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else-if="scope.row.Module==1">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div class="chkText" v-if="item.Type==1&&belong==3">
                                出游人支持证件类型：
                                <glui-checkbox-group v-model="item.Values">
                                    <glui-checkbox label="1">身份证</glui-checkbox>
                                    <glui-checkbox label="2">护照</glui-checkbox>
                                    <glui-checkbox label="3">台胞证</glui-checkbox>
                                    <glui-checkbox label="4">回乡证</glui-checkbox>
                                </glui-checkbox-group>
                            </div>
                            <div class="chkText" v-else-if="item.Type==1&&belong==2">
                                (由于服务能力限制，本产品网上报价仅适用于持有大陆居民身份证的游客，不接受外籍游客（含港澳台同胞）预定，敬请谅解！)
                            </div>
                            <div class="chkText" v-else>
                                本产品网上报价适用持有大陆居民身份证的游客。如您持有其他国家或地区的护照，请在预订过程中注明。
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else-if="scope.row.Module==2">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div class="chkText" v-if="item.Type==9">
                                由于服务能力限制，行程强度较大，为防止旅途中发生人身意外伤害事故的问题，出游人年龄在
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 周岁（含）至
                                <input type="number" class="input  input-small" v-model="item.MaxValue"> 周岁（不含），可以本人自行在线预订，年龄范围以外的出游人
                                <font style="color:red;">不能预订</font>，敬请谅解！
                            </div>
                            <div class="chkText" v-else-if="item.Type==10">
                                由于服务能力限制，行程强度较大，为防止旅途中发生人身意外伤害事故的问题，出游人年龄在
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 周岁（含）至
                                <input type="number" class="input  input-small" v-model="item.MaxValue"> 周岁（不含），可以本人自行在线预订，年龄范围以外的出游人
                                <font style="color:red;">现询确认</font>，敬请谅解！
                            </div>
                            <div class="chkText" v-else-if="item.Type==11">
                                出游人年龄超过
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 周岁，须与我司签订《健康协议》！
                            </div>
                            <div class="chkText" v-else-if="item.Type==12">
                                由于服务能力限制, 此线路行程强度较大，为防止旅途中发生人身意外伤害事故的问题，如出行人中有
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 周岁(含)以下青少年、
                                <input type="number" class="input  input-small" v-model="item.MaxValue"> 周岁(含)以上老人，须至少各有1位
                                <input type="number" class="input  input-small" v-model="item.MinAge"> 周岁至
                                <input type="number" class="input  input-small" v-model="item.MaxAge"> 周岁亲友陪同方可参团，敬请谅解！
                            </div>
                            <div class="chkText" v-else-if="item.Type==2">
                                本产品不接受
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 岁以上的客人预订，敬请原谅！
                            </div>
                            <div class="chkText" v-else-if="item.Type==3">
                                本产品不接受
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 岁以下的客人预订
                            </div>
                            <div class="chkText" v-else-if="item.Type==4">
                                本产品不接受
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 岁以上的客人单独报名出游
                            </div>
                            <div class="chkText" v-else-if="item.Type==5">
                                本产品不接受
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 岁以下的客人单独报名出游
                            </div>
                            <div class="chkText" v-else-if="item.Type==6">
                                本产品只接受
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 岁（含）--
                                <input type="number" class="input  input-small" v-model="item.MaxValue"> 岁（含）的客人报名出游
                            </div>
                            <div class="chkText" v-else-if="item.Type==21">
                                由于服务能力限制，无法接待
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 周岁以上的旅游者报名出游，敬请谅解.
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else-if="scope.row.Module==3">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div class="chkText" v-if="item.Type==13">
                                由于服务能力限制，单订单人数少于
                                <input type="number" class="input  input-small" v-model="item.MinValue" />人时无法成团，故不能预订，敬请谅解！
                            </div>
                            <div class="chkText" v-else-if="item.Type==14">
                                由于服务能力限制，单订单人数多于
                                <input type="number" class="input  input-small" v-model="item.MinValue" />人时无法接待，故不能预订，敬请谅解！
                            </div>
                            <div class="chkText" v-else-if="item.Type==15">
                                由于服务能力限制，单订单人数少于
                                <input type="number" class="input  input-small" v-model="item.MinValue" />人时需现询确认，敬请谅解！
                            </div>
                            <div class="chkText" v-else-if="item.Type==16">
                                由于服务能力限制，单订单人数多于
                                <input type="number" class="input  input-small" v-model="item.MinValue" />人时需现询确认，敬请谅解！
                            </div>
                            <div class="chkText" v-else-if="item.Type==7">
                                单订单人数少于
                                <input type="number" v-model="item.MinValue" />人时无法预定
                            </div>
                            <div class="chkText" v-else-if="item.Type==8">
                                由于服务能力限制，单订单人数多于
                                <input type="number" class="input  input-small" v-model="item.MinValue" />人时无法成团，敬请谅解
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else-if="scope.row.Module==4">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div v-if="item.Type==17" class="chkText">
                                由于服务条件限制 ，本产品
                                <glui-select v-model="item.IsAccept" placeholder="请选择">
                                    <glui-option :value="0" label="不接受">
                                    </glui-option>
                                    <glui-option :value="1" label="接受">
                                    </glui-option>
                                </glui-select>
                                <glui-select v-model="item.Values" multiple filterable remote :remote-method="queryCity" placeholder="请选择">
                                    <glui-option :key="city.id" :value="city.id" :label="city.name" v-for="city in citySource">
                                    </glui-option>
                                </glui-select>
                                户籍的游客预订，敬请谅解！
                            </div>
                            <div v-else class="chkText">
                                由于服务条件限制 ，本产品不接受
                                <input type="text" class="input  input-medium" v-model="item.Residence" /> 的游客预订，敬请谅解！
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else>
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div style="display: inline-block;width: 97%;padding-left: 10px;">
                                <p :key="index" v-for="(val,index) in item.Values">
                                    {{val}}
                                </p>
                            </div>
                        </div>
                    </li>
                </ul>
            </template>
        </glui-table-column>
    </glui-table>

</template>
<script>
import httputils from "@/js/httputils";
export default {
    data: function () {
        return {
            belong: 1,
            modules: [],
            citySource: [],
            AgeLimitType: 0
        };
    },
    props: {
        productid: {
            type: [Number, String],
            default: 0
        },
        pname: {
            type: String,
            default: "预订限制"
        }
    },
    methods: {
        queryCity: function (query) {
            var that = this;
            if (!query || query.length <= 0) {
                return;
            }
            httputils.get('/travel/supergoproduct/Common/GetCity',
                {
                    key: query,
                    isonlydiji: true,
                    type: 1
                }).then(result => {
                    var lstCity = [];
                    if (result) {
                        result.forEach(item => {
                            lstCity.push({
                                id: item.id.toString(),
                                name: item.name
                            });
                        });
                        // that.$nextTick(() => {
                        //     cb && cb();
                        // });
                    }
                    that.citySource = lstCity;
                });
        },
        loadDatas() {
            var that = this;
            return new Promise((resolve, reject) => {
                httputils.get('/travel/supergoproduct/BookLimit/GetLimits', {
                    pid: that.productid
                }).then(resp => {
                    if (resp && resp.Result) {
                        that.belong = resp.Result.ProjectBelong
                        that.modules = resp.Result.Modules
                        that.modules && that.modules.forEach(module => {
                            if (module.Module === 4) {
                                module.Items && module.Items.forEach(item => {
                                    if (item.Type === 17) {
                                        let ids = item.Values;
                                        if (item.Values && item.Values.length >= 1) {
                                            that.queryCity(item.Values.join(","));
                                        }
                                    }
                                });
                            }
                        });
                        //if (that.belong == 2) {
                        //    $.each(that.modules, function (i, item) {
                        //        if (item.Module == 2) {
                        //            $.each(item.Items, function (i, k) {
                        //                if (k.IsCheck && (k.Type == 9 || k.Type == 10 || k.Type == 12 || k.Type == 22)) {
                        //                    that.AgeLimitType = k.Type;
                        //                }
                        //            });
                        //        }
                        //    });
                        //}

                        resolve()
                    } else {
                        reject(`${that.pname}:${resp.ErrorMsg}`)
                    }
                }).catch(err => {
                    console.log(err);
                    reject(`${that.pname}加载异常`)
                })
            });
        },
        saveData() {
            var that = this;
            //if (that.belong == 2) {
            //    $.each(that.modules, function (i, item) {
            //        if (item.Module == 2) {
            //            $.each(item.Items, function (i, k) {
            //                if (that.AgeLimitType == k.Type) {
            //                    k.IsCheck = true;
            //                }
            //            });
            //        }
            //    });
            //}
            return new Promise((resolve, reject) => {
                httputils.post("/travel/supergoproduct/BookLimit/SaveLimits",
                    {
                        request: that.modules,
                        pid: that.productid
                    }).then(res => {
                        resolve({ success: res && res.IsSuccess, msg: `${that.pname}:${res.ErrorMsg}` });
                    }).catch(err => {
                        resolve({ success: false, msg: `${that.pname}:${err}` });
                    });
            });
        }
    }
}
</script>

<style scoped>
</style>





